<template>
  <PieChart
  v-if="this.isLoaded"
  v-bind:data="this.chartData"
  v-bind:labels="this.chartLabels"
  v-bind:color="this.color"
  />
</template>

<script>
import PieChart from '../Charts/PieChart.vue'
export default {
  name: "TeamsPieChart",
  components: {
    PieChart
  },
  mounted() {
    this.chartLabels = ["Team A", "Team B", "Team C", "Team D"]
    this.chartData = [10, 8, 11, 9]
    this.color = ["#003f5c", "#58508d", "#bc5090", "#ff6361", "#ffa600"]
    this.isLoaded = true
  },
  data() {
    return {
      isLoaded: false,
      chartData: [],
      chartLabels: [],
      color: []
    }
  }
}
</script>

